@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400&display=swap);
.button {
  background: none;
  outline: none;
  border: none;
  color: var(--black-color);
  font-size: 1.1rem;
  cursor: pointer;
}

.button:not(.button.icon) {
  border: 0.2rem solid var(--black-color);
  border-radius: 0.2rem;
  padding: 0.2rem 0.5rem;
}

.button:not(.button.icon):hover {
  background-color: var(--black-color);
  color: var(--white-color);
}

.button.icon.success:hover {
  color: var(--success-color);
}

.button.icon.danger:hover * {
  color: var(--danger-color) !important;
}

.search-form {
  display: flex;
  flex-direction: column;
  border: 0.2rem solid var(--black-color);
  padding: 1rem;
}

.search-form-button {
  margin-top: 1rem;
}

.search-form-button:hover {
  background-color: var(--success-color) !important;
  border-color: var(--success-color) !important;
}

.search-form .button.icon {
  align-self: flex-end;
  position: absolute;
}

.search-form label {
  font-size: 1.2rem;
  padding-right: 5rem;
  padding-top: 1rem;
}

.search-latest-launches {
  background: none;
  outline: none;
  border: none;
  font-size: 1.1rem;
  cursor: pointer;
  border-bottom: 0.1rem solid var(--success-color);
}

header {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 2rem;
  padding: 0.5rem 0;
  padding-left: 1rem;
  border-bottom: 0.3rem solid var(--black-color);
}

@media (max-width: 767px) {
  header .app-title {
    font-size: 2rem;
  }
}

header > * {
  margin-top: 0.5rem;
}

.card-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
}

.card {
  box-shadow: var(--card-shadow-03dp);
  border-radius: 0.5rem;
  flex-basis: 30%;
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  cursor: pointer;
}

.card:hover {
  box-shadow: var(--card-shadow-08dp);
}

.card-image {
  max-width: 100%;
  height: auto;
}

.card-image-container {
  max-height: 20rem;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: thin;
}

@media (max-width: 767px) {
  .card,
  .card.expanded {
    flex-basis: 80%;
  }
}

.card .close-card-button {
  align-self: flex-end;
  position: absolute;
}

.close-card-button * {
  color: var(--white-color);
}

.card-name {
  align-self: center;
  padding: 0.3rem;
}

.card-date {
  padding: 0.5rem 0.5rem;
  font-size: 0.8rem;
  align-self: flex-end;
  font-weight: var(--primary-font-weight);
}

.card-details {
  padding: 0.5rem;
  line-height: 1.3;
}

.card-link-button {
  min-width: 50%;
  align-self: center;
  outline: none;
  text-decoration: none;
  text-align: center;
  margin: 1rem 0;
}

:root {
  --font: 'Noto Sans', sans-serif;
  --primary-font-weight: 700;
  --secondary-font-weight: 400;

  --success-color: #4ac631;
  --danger-color: #ff4732;
  --white-color: #ffffff;
  --black-color: #333333;

  --card-border-radius: 1.25rem;
  --card-shadow-03dp: 0px 3px 4px rgba(0, 0, 0, 0.14),
    0px 3px 3px rgba(0, 0, 0, 0.12), 0px 1px 8px rgba(0, 0, 0, 0.2);
  --card-shadow-08dp: 0px 8px 10px rgba(0, 0, 0, 0.14),
    0px 3px 14px rgba(0, 0, 0, 0.12), 0px 5px 5px rgba(0, 0, 0, 0.2);
}

html {
  font-size: 100%;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 700;
  font-weight: var(--primary-font-weight);
  line-height: 1.3;
}

h3,
h4,
h5 {
  font-weight: 400;
  font-weight: var(--secondary-font-weight);
}

h1 {
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small {
  font-size: 0.8rem;
}

* {
  font-family: 'Noto Sans', sans-serif !important;
  font-family: var(--font) !important;
  box-sizing: border-box;
  color: #333333;
  color: var(--black-color);
}

